<template>
  <div id="Change">
    <p>是否确定{{ operate }}?</p>
    <div>
      <button id="first" @click="change(operate)">是</button>
      <button id="second" @click="cancel(biggest)">否</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "Change",
  props: {
    operate: String,
    biggest: String,
  },
  methods: {
    cancel: function (biggest) {
      document.getElementById(biggest).style.zIndex = "0";
      document.getElementById(biggest).style.opacity = "100%";
      document.getElementById("Change").style.display = "none";
      console.log(this.$store.state.createPost);
       this.$store.state.createPost.posterId=parseInt(localStorage.getItem("userId"));
       console.log(this.$store.state.createPost);
    },
    change: function (operate) {
     if(operate=="注册"){
        fetch("http://180.76.100.8/users/register", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(this.$store.state.personalInformation),
        })
          .then((res) => {
            console.log(res);
          });
     }
     if(operate=="添加"){
       fetch("http://180.76.100.8/users/"+localStorage.getItem("userId"),{
         method:"GET"
       })
       .then((res) => res.json())
        .then((res)=>{
          this.$store.state.createPost.posterName=(res.data.nickname);
          console.log(this.$store.state.createPost);
        })
      }
       this.$store.state.createPost.posterId=parseInt(localStorage.getItem("userId"));
    this.$store.state.createPost.flag=0;
    console.log(this.$store.state.createPost);
       fetch("http://180.76.100.8/posts",{
         method:"POST",
         headers:{
           "Content-type":"application/json"
         },
         body:JSON.stringify(this.$store.state.createPost),
       })
       .then((res)=>{
         console.log(res);
       })
     
      alert("已" + operate + "!");
      this.$router.go(-1);
    },
  },
};
</script>
<style scoped>
#Change {
  position: fixed;
  top: 20%;
  background-color: #ffffff;
  width: 60%;
  margin: 20%;
  height: 18%;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  z-index: 2;
}
p {
  margin-bottom: 10pt;
}
#first {
  margin-right: 20pt;
  width: 50pt;
  height: 20pt;
  background-color: #cccccc;
  border: none;
  border-radius: 4pt;
  color: #ffffff;
  outline: none;
}

#second {
  width: 50pt;
  height: 20pt;
  background-color: #bf544a;
  border: none;
  border-radius: 4pt;
  color: #ffffff;
  outline: none;
}
</style>